<template>
    <div :class="{'recommend-container':true,'marginTop60':showComprehensive || showFilter}">
        <div class="recommend-merchant">推荐商家</div>
        <div class="recommend-tab">
            <div class="all-recommend" ref="all">
                <a href="javascript:void(0);" v-for="(item,index) in recommendTitleList" @touchend="showContentList(index)">
                    <span :class="recommendIndex === index ? 'active':''">{{item.title}}</span>
                    <i v-if="item.icon" :class="item.icon"></i>
                </a>
            </div>
            <section class="comprehensive-list" v-if="showComprehensive" @touchend="closeComprehensiveAndFilter">
                <ul>
                    <li v-for="(item,index) in comprehensiveList" @touchend.stop="selectComprehensive(index)">
                        <span :class="{'active':selectIndex == index}">{{item}}</span>
                        <i v-if="selectIndex == index" class="el-icon-check"></i>
                    </li>
                </ul>
            </section>
            <section class="filtrate-container" v-if="showFilter" @touchend="closeComprehensiveAndFilter">
                <div class="filter-list">
                    <dl v-for="(item,index) in filterList">
                        <dt>{{item.title}}</dt>
                        <dd v-for="infoItem in item.info">
                            <div class ="filter-item" @touchend.stop="selectService(infoItem,index)">
                                <img v-if="infoItem.imgSrc" :src="infoItem.imgSrc" alt="">
                                <span :class="{'active':infoItem.isSelected}">{{infoItem.desc}}</span>
                            </div>
                        </dd>
                    </dl>
                </div>
                <div class="select-btn">
                    <span @touchend.stop="clearFilter">清空</span>
                    <span class="confirm" @touchend.stop="confirmFilter">确认</span>
                </div>
            </section>
        </div>

        <section class="recommend-food-list" v-if="!showComprehensive && !showFilter">
            <section class="recommend-food-item" v-for="item in recommendList">
                <div class="recommend-food-info">
                    <div class="recommend-food-img">
                        <img :src="item.imgSrc" alt="">
                    </div>
                    <div class="recommend-food-desc">
                        <section class="recommend-des">
                            <!-- <router-link :to="{name:'merchant',params:{id:item.title}}"> -->
                                <h4 @touchend="jumpTo(item.title)">{{item.title}}</h4>
                            <!-- </router-link>  -->
                            <span @touchend="showIsLike(item)">···</span>
                            <div class="recommend-islike" v-if="item.isLike" @touchend="showIsLike(item)">
                                <span>不喜欢</span>
                            </div>
                        </section>
                        <section class="recommend-des">
                            <div class="recommend-rate-sales">
                                <div class="rate">
                                    <el-rate
                                        v-model="item.rateValue"
                                        disabled
                                        show-score
                                        text-color="#ff9900"
                                        score-template="{value}">
                                    </el-rate>
                                </div>
                                <div class="month-sale">月售{{item.sale}}单</div>
                            </div>
                            <div class="peisong">蜂鸟专送</div>
                        </section>
                        <section class="recommend-des">
                            <div class="peisong-price">
                                <span class="song">¥{{item.priceSong}}起送</span>
                                <span class="pei">配送费¥{{item.peiPrice}}</span>
                            </div>
                            <div class="distance-time">
                                <span class="song">{{item.distance}}</span>
                                <span class="pei">{{item.needTime}}</span>
                            </div>
                        </section>
                    </div>
                </div>
                <div class="recommend-food-discount">
                    <div class="show-discount-container">
                        <div v-for="(activeItem,activeIndex) in item.activeInfo">
                            <div class="show-discount-item" v-if="item.isSpred || activeIndex < 2 ">
                                <span class="show-discount-detail" >
                                    <span :class="['tong',activeItem.className]">{{activeItem.mainWord}}</span>
                                    <span>{{activeItem.desc}}</span>
                                </span>
                            </div>
                        </div>  
                    </div>
                    <div class="discount-active" v-if="item.activeInfo.length>2?true:false" @touchend="toggleDiscountInfo(item)">
                        <span>{{item.activeInfo.length}}个活动</span>
                        <i class="el-icon-caret-bottom" v-if="!item.isSpred"></i>
                        <i class="el-icon-caret-top" v-if="item.isSpred"></i>
                    </div>
                </div>
            </section> 
        </section>


    </div>

</template>

