                    },
                    {
                        imgSrc:'https://cube.elemecdn.com/6/7c/417ba499b9ef8240b8014a453bf30png.png?x-oss-process=image/format,webp/resize,w_24,h_24,m_fixed',
                        desc:'品牌商家',
                        isSelected:false
                    },
