                        desc:'食安保',
                        isSelected:false
                    },
                    {
                        imgSrc:'https://cube.elemecdn.com/b/9b/45d2f6ff0dbeef3a78ef0e4e90abapng.png?x-oss-process=image/format,webp/resize,w_24,h_24,m_fixed',
                        desc:'开发票',
