                    }
                ]
            }
        ],
        recommendList:[
            {
                imgSrc:'https://cube.elemecdn.com/d/7b/8ea48c631d66914603bb08a3d22bepng.png?x-oss-process=image/format,webp/resize,w_130,h_130,m_fixed',
                title:'贡茶',
                isLike:false,
                rateValue:3.9,
                sale:4339,
                priceSong:20,
                peiPrice:2,
                distance:'1.90km',
                needTime:'32分钟',
                isSpred:false,
                activeInfo:[
                    {
                        className:'shou',
                        mainWord:'首',
                        desc:'新用户下单立减15元'
                    },
                     {
                        className:'jian',
                        mainWord:'减',
                        desc:'满30减5，满48减8，满88减12'
                    },
                     {
                        className:'te',
                        mainWord:'特',
                        desc:'特价商品0.5元起'
                    },
                     {
                        className:'pei',
                        mainWord:'赔',
                        desc:'商家原因导致订单取消，赔付代金券'
                    },
                     {
                        className:'bao',
                        mainWord:'保',
                        desc:'该商户食品安全已由国泰产险承担，食品安全有保障'
                    },
