                        desc:'该商户食品安全已由国泰产险承担，食品安全有保障'
                    }
                ]
            },
             {
                imgSrc:'https://cube.elemecdn.com/d/7b/8ea48c631d66914603bb08a3d22bepng.png?x-oss-process=image/format,webp/resize,w_130,h_130,m_fixed',
                href:'#',
                title:'贡茶',
                isLike:false,
                rateValue:3.9,
                sale:4339,
                priceSong:20,
                peiPrice:2,
                distance:'1.90km',
                needTime:'32分钟',
                isSpred:false,
                activeInfo:[
                    {
                        className:'shou',
                        mainWord:'首',
                        desc:'新用户下单立减15元'
                    },
                     {
                        className:'jian',
                        mainWord:'减',
                        desc:'满30减5，满48减8，满88减12'
                    },
                     {
                        className:'te',
                        mainWord:'特',
                        desc:'特价商品0.5元起'
                    },
                     {
                        className:'pei',
                        mainWord:'赔',
                        desc:'商家原因导致订单取消，赔付代金券'
                    },
                     {
                        className:'bao',
                        mainWord:'保',
                        desc:'该商户食品安全已由国泰产险承担，食品安全有保障'
                    },
                     {
                        className:'bao',
                        mainWord:'保',
                        desc:'该商户食品安全已由国泰产险承担，食品安全有保障'
                    }
                ]
            }
        ],
        confirmList:[],
        h:0
      }
    },
    mounted(){
        this.h = this.$refs.all.offsetTop
        console.log(this.h)
    },
    methods:{
        toggleDiscountInfo(item){
            item.isSpred = !item.isSpred;
        },
        showIsLike(item){
            item.isLike = !item.isLike;
        },
        showContentList(index){
            this.recommendIndex = index;
            this.showComprehensive = false;
            this.showFilter = false;
            
            if(index === 0){
                this.showComprehensive = true;
                // window.scroll(0,this.h);
                // // console.log(window.scroll)
                // console.log(this.h, window.pageYOffset)
            }else if(index === this.recommendTitleList.length - 1){
                this.showFilter = true;
            }else{
                this.selectIndex = -1;
            }
            this.changeComprehensive(this.showComprehensive);
            this.changeFilter(this.showFilter);
        },
        selectComprehensive(index){
            this.selectIndex = index;
            this.showComprehensive = false;
            this.changeComprehensive(this.showComprehensive);
            this.changeFilter(this.showFilter);
        },
        closeComprehensiveAndFilter(){
            this.showComprehensive = false;
            this.showFilter = false;
            this.changeComprehensive(this.showComprehensive);
            this.changeFilter(this.showFilter);
        },
        selectService(infoItem,index){
            if(index != 0){
                this.filterList[index].info.map(ele => {
                    return ele.isSelected = false;
                });
                this.confirmList = this.confirmList.filter(it =>{
                    return this.filterList[index].info.every(ele =>{
                        return ele.desc !== it;
                    })
                })
                let idx = this.deleteRepeat(this.confirmList,this.filterList[index].info)
                idx && this.confirmList.splice(idx,1);
            }
            infoItem.isSelected = true;
            this.confirmList.push(infoItem.desc);
        },
        deleteRepeat(arr1,arr2){
            for(let i = 0; i < arr1.length; i++){
                for(let j = 0; j < arr2.length; j++){
                    if(arr1[i] === arr2[j].desc){
                        return i;
                    }
                }
            }
        },
        clearFilter(){
            this.filterList.map(ele =>{
                return ele.info.map(e =>{
                    return e.isSelected = false;
                })
            })
            this.confirmList = [];
        },
        confirmFilter(){
            console.log(this.confirmList);
        },
        jumpTo(title){
            this.$router.push({
                name:'shop',
                params:{
                    content:title
                }
            })
        },
        ...mapMutations(['changeComprehensive','changeFilter'])
    }
}
</script>

<style scoped lang="scss">
    @import '@/assets/css/home/elemRecommend.scss';  
</style>
<style >
     .el-rate__icon.el-icon-star-on{
        margin-right:0 !important;
        font-size:0.28rem !important;
    }
     .el-rate__text {
        font-size: 0.24rem;
        color:#666!important;
    }
    .el-rate{
        height:auto;
    }
</style>